import React from "react";
import "./styles.css";

const QuestionsList = ({ questions, onQuestionPress, currentIndex }) => {
  const mapQuestions = questions.map((question, index) => (
    <li
      className={currentIndex === index ? "active" : null}
      onClick={() => onQuestionPress(index)}
      key={question.id}
    >
      Q{question.id}
    </li>
  ));
  return (
    <div className="scroll-container">
      <ol>{mapQuestions}</ol>
    </div>
  );
};

export default React.memo(QuestionsList);
