import React, { useEffect, useRef, useState } from "react";
import "./style.css";

const TopBar = ({
  totalQuestions,
  currentQuestionNumber,
  canGoPrevious,
  canGoNext,
  getPreviousQuestion,
  getNextQuestion,
  onNotePress,
  onLabValuesPress,
}) => {
  const [showFullScreen, setShowFullScreen] = useState(false);
  const bodyRef = useRef(document.body);
  const openFullscreen = () => {
    const body = bodyRef.current;

    if (body.requestFullscreen) {
      body.requestFullscreen();
    } else if (body.webkitRequestFullscreen) {
      /* Safari */
      body.webkitRequestFullscreen();
    } else if (body.msRequestFullscreen) {
      /* IE11 */
      body.msRequestFullscreen();
    }
  };

  const closeFullscreen = () => {
    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.webkitExitFullscreen) {
      /* Safari */
      document.webkitExitFullscreen();
    } else if (document.msExitFullscreen) {
      /* IE11 */
      document.msExitFullscreen();
    }
  };

  useEffect(() => {
    if (showFullScreen) {
      openFullscreen();
    } else if (
      document.fullScreen ||
      document.mozFullScreen ||
      document.webkitIsFullScreen
    ) {
      closeFullscreen();
    }
  }, [showFullScreen]);

  return (
    <div className="top-bar">
      <div className="item">
        <p className="bold">
          Item: {currentQuestionNumber} of {totalQuestions}
        </p>
        <span>QID: 47593</span>
      </div>
      <div style={{ flexGrow: 1 }} className="buttons">
        <button disabled={!canGoPrevious} onClick={getPreviousQuestion}>
          <img alt="" src="/assets/icons/left-arrow.png" />
          <span>Previous</span>
        </button>

        <button disabled={!canGoNext} onClick={getNextQuestion}>
          <img alt="" src="/assets/icons/right-arrow.png" />
          <span>Next</span>
        </button>
      </div>
      <div className="buttons">
        <button
          className="_hidden"
          onClick={() => setShowFullScreen(!showFullScreen)}
        >
          <img
            alt=""
            src={
              showFullScreen
                ? "/assets/icons/fullscreen-exit.png"
                : "/assets/icons/fullscreen.png"
            }
          />
          <span>{showFullScreen ? "Exit Fullscreen" : "Fullscreen"}</span>
        </button>
        <button className="_hidden">
          <img alt="" src="/assets/icons/tutorial.png" />
          <span>Tutorial</span>
        </button>
        <button onClick={onLabValuesPress}>
          <img alt="" src="/assets/icons/labs.png" />
          <span>Lab Values</span>
        </button>
        <button onClick={onNotePress}>
          <img alt="" src="/assets/icons/edit.png" />
          <span>Note</span>
        </button>
      </div>
    </div>
  );
};

export default React.memo(TopBar);
